body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  margin: 0;
  width: 100%;
  height: 100%;
}

* {
  box-sizing: border-box;
}
p {
  margin: 0;
  padding: 0;
}

#root {
  min-height: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.App {
  width: 100%;
  min-height: 100%;
  margin-top: 2%;
  display: -webkit-flex;
  display: flex;

  -webkit-justify-content: center;

          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.credits {
  width: 80%;
  max-width: 370px;
  margin: 2% auto;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}
.credits-link {
  margin-left: 0.2em;
  margin-right: 0.2em;
}

.card {
  position: relative;
  margin: 1rem;
  min-width: 23.7rem;
  min-height: 33rem;
  background-image: url(/combat-description-cards/static/media/fire2.f584a2b0.jpg);
  background-size: cover;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 0px 15px 2px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.card__content-wrapper {
  min-height: 31.25rem;
  width: 21.8rem;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background-color: rgba(225, 186, 65, 0.91);
  font-family: "Roboto", sans-serif;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding-top: 1.25rem;
}
.card__btn {
  width: 1.875rem;
  height: 1.875rem;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-bottom: 3px solid rgba(0, 0, 0, 0.5);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: rgb(205, 169, 60);
}
.card__btn:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}
.btn--selected {
  outline: 3px solid black;
}
.btn-new,
.btn-close {
  width: 1.25rem;
  height: 1.25rem;
  position: absolute;
  top: 0;
}
.btn-new {
  right: 0;
}
.btn-close {
  left: 0;
}
.card__wrapper {
  width: 100%;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 5px 0;
}
.card__name-wrapper {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.name-edit {
  width: 100%;
  display: -webkit-flex;
  display: flex;
}
.card__name {
  width: 100%;
  padding: 5px 0;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}
.name-input {
  width: 100%;
  padding: 5px 0;
  margin: 0 5px 0 3.125rem;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}
.btn-save-name {
  width: 3.75rem;
}
.weapon-btn,
.style-btn {
  width: 3.125rem;
  height: 3.125rem;
  margin-left: 10px;
}
.reload-btn {
  width: 11.25rem;
}
.description-wrapper {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
.card__description {
  width: 100%;
  padding: 5px 10px;
  text-align: center;
}
.focus-word {
  font-size: 1.1rem;
  font-weight: bold;
  text-align: center;
}
.weapon-btn,
.style-btn {
  background-repeat: no-repeat;
  background-size: 90%;
  background-position: center center;
}
.slashing {
  background-image: url(/combat-description-cards/static/media/sword.cde61948.svg);
}
.piercing {
  background-image: url(/combat-description-cards/static/media/spear.aa8d171f.svg);
}
.blunt {
  background-image: url(/combat-description-cards/static/media/hammer.7ab3977b.svg);
}

.power {
  background-image: url(/combat-description-cards/static/media/power.89470ca1.svg);
}

.finesse {
  background-image: url(/combat-description-cards/static/media/finesse.6a77af33.svg);
}

.range {
  background-image: url(/combat-description-cards/static/media/bow.caf7e909.svg);
}

@media (max-width: 600px) {
  html {
    font-size: 10px;
  }
  .btn--selected {
    outline: 1px solid black;
  }
}

